      <div class="f-con-list img-bg" >
        <div class="f-con-item">
          <span class="name">LP {{ $t("message.banner") }}: {{ HbyHtInfo ? HbyHtInfo.HbyHtBalance : 0 | price }} LP</span>
          <span class="num" style="color: #a6a6ff" @click="max"
            >MAX</span
          >
        </div>

        <div class="f-con-input">
          <van-field
            v-model="stakeAmount"
