           <!-- <div class="submitBtn2" @click="lpApprove">
            {{ $t("message.authorization") }}
          </div> -->
          <div class="submitBtn" @click="getReward">
            {{ $t("message.tq") }}
          </div>
        </div>

         <div class="f-con-item">
          <div>
            <span class="name">LP {{ $t("message.banner") }}</span>
