                cheBalance ? cheBalance : 0 | price
              }} CHE</span>
        </div>

        <div class="f-con-item">
          <div>
            <span class="name">{{ $t("message.unExtract") }}</span>
          </div>
          <span class="num">{{ pool2Info ? pool2Info.info[16] : 0 | figure }} CHE</span>
        </div>
        <div class="f-con-item">
          <div>
            <span class="name">{{ $t("message.allEarn") }} </span>
          </div>
          <span class="num">{{ pool2Info ? pool2Info.info[8] : 0 | figure }} CHE</span>
        </div>
          <!-- <div class="f-con-item" v-if="isMaster">
          <div>
            <span class="name" >{{ $t("message.jl") }}</span>
          </div>
          <span class="num">{{ pool2Info ? pool2Info.info[20] : 0 | figure }} CHE</span>
        </div> -->
        <div class="f-con-item">
          <div>
            <span class="name">{{ $t("message.myZy") }}</span>
          </div>
          <span class="num">{{ pool2Info ? pool2Info.info[6] : 0 | figure }} LP</span>
        </div>

        <div
          class="f-con-btn"
          style="justify-content: flex-start; margin: 40px 0"
        >
          <!-- <div class="submitBtn" @click="lpApprove">授权</div> -->
          <div class="submitBtn2" @click="withdraw">
            {{ $t("message.exitPledge") }}
          </div>
        </div>
      </div>
    </div>

    <!-- 我的团队 -->
    <!-- <div class="f-con temaBox">
      <h1 class="tema">{{ $t("message.usage") }}</h1>
      <div class="f-con-list">
      
        <div class="f-con-item">
          <div>
            <span class="name">{{ $t("message.circleLength") }} </span>
          </div>
          <span class="num">{{ pool2Info ? pool2Info.info[13] : '--' }}</span>
        </div>
        <div class="f-con-item">
          <div>
            <span class="name">{{ $t("message.circleAddr") }}</span>
          </div>
          <span class="num">{{ pool2Info ? protectAddress(pool2Info.poolAddr, 10) :'--'}}</span>
        </div>
        <div class="f-con-item">
          <div>
            <span class="name">{{ $t("message.link") }}</span>
          </div>
          <span class="num">{{ link }}</span>
        </div>
        <div class="f-con-btn">
          <div
            class="submitBtn cursor"
            v-clipboard:copy="link"
            v-clipboard:success="onCopy"
            v-clipboard:error="onError"
          >
            {{ $t("message.copyLink") }}
          </div>
        </div>
      </div>
    </div> -->

    <div class="Popup" v-if="isShowEstablish">
      <div class="mark" id="mark"></div>
      <div class="con popIn" id="con">
        <div class="title">
          {{ $t("message.cjCircle") }}
        </div>
        <div class="txt-p" v-if="lang == 'zhCHS'">
          <p>
            1、销毁10枚CHE即可成为圈主，圈子码可自由定制，完成后建议转发到各大社交媒体提高圈子影响力。
          </p>
          <p>
            2、圈子创建完成后，圈主在"CHE/USDT
            LP"处进行授权，然后点击质押即可挖矿。
          </p>
          <p>
            3、圈主能获得圈子内成员每次提现CHE的10%，成为全球合伙人可赚取更多收益。
          </p>
        </div>
        <div class="txt-p" v-else>
          <p>
            1. Destroy 10 CHEs to become the circle master, and the circle code
            can be customized freely. After completion, it is suggested to
            forward it to major social media to improve the circle influence.
          </p>
          <p>
            2. After the circle is created, the circle owner will authorize at
            "CHE/USDT LP", and then click "Pledge" to mine.
          </p>
          <p>
            3. The owner of the circle can get 10% of the CHE withdrawn by each
            member of the circle, and become a global partner to earn more
            profits.
          </p>
        </div>
        <div class="content">
          <input
            type="number"
            :placeholder="$t('message.circleCode')"
            v-model="creationCode"
            disabled="disabled"
          />
          <div class="sc" @click="randomNum">{{ $t("message.random") }}</div>
        </div>
        <div class="footer">
          <div class="btn cancel" @click="hidePop">
            {{ $t("message.cancel") }}
          </div>
          <div class="btn confirm" @click="establish(2)" v-if="cheApprove">
            {{ $t("message.ok") }}
          </div>
          <div class="btn confirm" @click="cheApproveFun" v-else>
            {{ "CHE" + $t("message.authorization") }}
          </div>
        </div>
      </div>
    </div>

    <FontCon />
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import FontCon from "../../components/Footer";
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: "",
      type: "home",
      timer2: null,
      // localR: "",
      // localR2: "",
      isApprove: false,
      wokt: "0",
      Apy: 0,
      isHome: true,
      stakeAmount: 0,
      hylink: "0x4b26a8d7be71b2a221e468e9f9f05d9af9c9229b",
      link: "--",
      isMaster: false,
      isShowEstablish: false,
      creationCode: "",
      cheApprove: false,
    };
  },
  computed: {
    ...mapState("main", [
      "pool2Info",
      "pool2Contract",
      "account",
      "cheBalance",
      "HBYBalance",
      "cuseprice",
      "HBYCoinContract",
      "cheCoinContract",
      "HbyHtInfo",
      "HbyHtCoinContract",
    ]),
  },
  components: {
    HeadTop,
    FontCon,
  },
  methods: {
    ...mapActions("main", [
      "add_price",
      "add_pool2Info",
      "add_HBYCoinContract",
      "add_cheCoinContract",
      "add_HbyHtCoinContract",
    ]),
    randomNum: function () {
      var code = "";
      for (var i = 1; i <= 6; i++) {
        const num = Math.floor(Math.random() * 10);
        code += num;
      }
      this.creationCode = code;
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + hash;
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    apyFun() {
      if(!this.pool2Info) return
      var all = (
        this.pool2Info.rewardRate *
        Number(this.cuseprice) *
        365
      ).toFixed(8);
      var zhiya = (this.pool2Info.info[1] / 10 ** 18).toFixed(2);
      
      if (all != 0 && zhiya != 0) {
        var apy = all / zhiya;
        this.Apy = apy;
      } else {
        return 0;
      }
    },
    cheApproveFun: function () {
      if (this.cheCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.pool2Info.address;
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var account = this.account;
      this.cheCoinContract.methods
        .approve(address, "100000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          // console.log("授权=====", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.cheApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权
    cheIsApproveFun: function () {
      if (this.pool2Info) {
        var account = this.account;
        var address = this.pool2Info.address;
        console.log(address)
        this.cheCoinContract.methods
          .allowance(account, address)
          .call()
          .then((res) => {
            var a = web3.utils.fromWei(res, "ether");
            if (a > 0) {
              this.cheApprove = true;
            }
          })
          .catch((err) => {
            this.$toast.clear();
            this.$toast(err.message);
            console.log(err);
          });
      }
    },
    max: function () {
      var num = this.HbyHtInfo.HbyHtBalance;
      num = num.toString();
      let index = num.indexOf(".");
      if (index !== -1) {
        num = num.substring(0, 4 + index + 1);
      } else {
        num = num.substring(0);
      }

      this.stakeAmount = num;
    },
    hidePop: function () {
      this.isShowEstablish = false;
      this.isShowJoin = false;
      this.creationCode = "";
      this.joinEstablish = "";
      this.partnerPop = false;
    },
    showPop: function (arg, callback) {
      // if (!this.cheBalance) return;
      if (arg == "showJoin") {
        if (this.$route.query.invite) {
          this.joinEstablish = this.$route.query.invite;
        }
        this.isShowJoin = true;
      } else if (arg == "showEstablish") {
        this.isShowEstablish = true;
      } else if (arg == "showPartnerPop") {
        this.partnerPop = true;
      } else if (arg == "show") {
        this.show = true;
      } else if (arg == "showPlay") {
        this.showPlay = true;
      }
    },
    // 		创建圈子
    establish: function (type) {
      if (this.pool2Info == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var myCode = 0; //我的圈子码/
      var code = 0; //创建圈子上级默认为0
      myCode = _this.creationCode;
      //2创建圈子
      if (!_this.creationCode) {
        this.$toast(this.$t("message.circleCode"));
        return;
      }
      if (Number(_this.cheBalance) < 10) {
        _this.$toast("CHE" + _this.$t("message.noBanner"));
        return;
      }

      _this.hidePop();
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      _this.pool2Contract.methods
        .isUsed(myCode)
        .call()
        .then((result) => {
          console.log(myCode,account,this.pool2Info.address)
          if (result) {
            _this.$toast(_this.$t("message.isUse"));
          } else {
            this.pool2Contract.methods
              .cretaePool(myCode)
              .send({ from: account })
              .then((res) => {
                _this.$toast.clear();
                _this.$toast.success(_this.$t("message.okCircle"));
                _this.add_pool2Info();
                // _this.getCoinBalance();
                _this.add_cheCoinContract();
                _this.getUrl(myCode);
                _this.isMaster = true;
                // console.log(myCode, _this.getUrl(myCode));
              })
              .catch((err) => {
                _this.$toast.clear();
                _this.$toast(err.message);
                console.log(err);
              });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getReward: function () {
      if (this.pool2Info == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var account = _this.account;
      this.pool2Contract.methods
        .getReward()
        .send({ from: account })
        .then((res) => {
          // console.log("getReward==============", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_pool2Info();
          // _this.getCoinBalance();
          _this.add_cheCoinContract();
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    //取消抵押
    withdraw: function () {
      if (this.pool2Info == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var myzy = web3.utils.fromWei(_this.pool2Info.info[6], "ether");
      if (myzy <= 0) {
        _this.$toast(_this.$t("message.noZy"));
        return;
      }
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.pool2Contract.methods
        .exit()
        .send({ from: account })
        .then((res) => {
          console.log("withdraw=======", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_pool2Info();
           _this.apyFun();
          _this.add_cheCoinContract();
          _this.add_HbyHtCoinContract();

          _this.withdrawAmount = 0;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    pirceInput(type) {
      if (type == 1) {
        let str = "" + this.stakeAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.stakeAmount = +str2;
        }
      } else {
        let str = "" + this.withdrawAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.withdrawAmount = +str2;
        }
      }
    },
    // 授权
    lpApprove: function () {
      if (this.pool2Info == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.pool2Info.address;
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var account = this.account;
      this.HbyHtCoinContract.methods
        .approve(address, "100000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("授权=====", res);
          if (res.status) {
            this.$toast.success(this.$t("message.accredit"));
            this.isApprove = true;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权
    isApproveFun: function () {
      if (this.pool2Info) {
        var account = this.account;
        var address = this.pool2Info.address;

        this.HbyHtCoinContract.methods
          .allowance(account, address)
          .call()
          .then((res) => {
            var a = web3.utils.fromWei(res, "ether");
            if (a.length >= 12) {
              this.isApprove = true;
            }
            console.log("a==========", a);
          })
          .catch((err) => {
            this.$toast.clear();
            this.$toast(err.message);
            console.log(err);
          });
      }
    },
    stake: function () {
      if (this.pool2Info == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var code = _this.pool2Info.inviteCode || _this.pool2Info.referrer;
      if (!_this.isApprove) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }

      if (!code || code == "--") {
         code = _this.$route.query.invite || localStorage.getItem('htInvite')
         code = code ? code.toString() :  '"1"';
      }
      if (Number(_this.stakeAmount) > Number(_this.HbyHtInfo.HbyHtBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }
      if (Number(_this.balance / 10 ** 18) < 0) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (!code || code == "--") {
        _this.$toast(_this.$t("message.onId"));
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      // var monye = (_this.stakeAmount * 10 ** 18).toString();
      console.log(code,web3.utils.toWei(_this.stakeAmount, "ether"))
      this.pool2Contract.methods
        .investIn(code, web3.utils.toWei(_this.stakeAmount, "ether"))
        .send({ from: account })
        .then((res) => {
          if (res.status) {
            _this.$toast.clear();
            _this.$toast.success(_this.$t("message.dealSuccee"));
            _this.add_pool2Info();
            _this.apyFun();
            _this.add_cheCoinContract();
            _this.add_HbyHtCoinContract();
          }

          _this.stakeAmount = 1;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
      this.$router.push({
        name: this.$route.name,
        params: { lang: this.lang },
      });
    },
    hreftwo(url) {
      this.$router.push({ path: url  });
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(36, 42)
        : addr;
    },
  },
  mounted() {
    var _this = this;
    var lang = localStorage.getItem("lang");
    var codeInvite = _this.$route.query.invite;
    if(codeInvite){
      localStorage.setItem('htInvite',codeInvite)
    }
    this.changeLanguage(lang || "zhCHS");
    if (!window.ethereum) {
      this.$notify({ type: "danger", message: this.$t("message.noMask") });
      return;
    } else {
      var web3 = new Web3(window.ethereum);
      web3.eth.net.getId().then((id) => {
        if (id != 128) {
          this.$notify({
            type: "danger",
            message: "Please switch the network!",
          });
          return;
        }
      });
    }
    this.add_price();
    var getData = () => {
      this.add_pool2Info();
      this.add_HBYCoinContract();
      this.add_cheCoinContract();
      this.add_HbyHtCoinContract();
      this.apyFun();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (
          !this.cheCoinContract ||
          !this.pool2Contract ||
          !this.cheBalance ||
          !this.pool2Info 
        ) {
          getData();
        } else {
          this.isApproveFun();
          this.cheIsApproveFun();
           
          getData();
          // console.log(this.pool2Info);
          if (!_this.pool2Info.inviteCode && _this.$route.query.invite) {
            _this.joinEstablish = _this.$route.query.invite;
          }
          if (_this.pool2Info.inviteCode || _this.pool2Info.referrer) {
            var invite = _this.pool2Info.inviteCode || _this.pool2Info.referrer;
            _this.getUrl(invite);
          }

          var oldArr = this.pool2Info.poolAddr.toLocaleLowerCase();
          var hasIndexOf = oldArr.indexOf(this.account);
          if (hasIndexOf == 0) {
            this.isMaster = true;
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

