import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/page/home/home'
import Usdt from '@/page/home/usdt'
import Ht from '@/page/home/ht'
import Pdf from '@/page/about/pdf'
import Rule from '@/page/about/rule'
Vue.use(Router);


//获取原型对象上的push函数
const originalPush = Router.prototype.push
    //修改原型对象中的push方法
Router.prototype.push = function push(location) {  
    return originalPush.call(this, location).catch(err => err)
}

export default new Router({
    routes: [
        {
            path: '/',
            name: 'index',
            component: Home
        },{
            path: '/rule/:lang',
            name: 'rule',
            component: Rule
        },
        {
            path: '/pdf/:lang',
            name: 'pdf',
            component: Pdf
        },
        {
            path: '/usdt',
            name: 'usdt',
            component: Usdt
        },
        {
            path: '/ht',
            name: 'ht',
            component: Ht
        },
       
    ]
})


