<template>
  <div class="home">
    <HeadTop
      @changeAppLocale="changeAppLocale"
      :type="currency"
      v-bind:lang="lang"
    />
    <img v-if="currency=='en'" src="../../assets/img/doswap/HDM_en.jpg" alt="" width="100%" style="margin-top:95px">
    <img v-else src="../../assets/img/doswap/HDM_zhCHS.jpg" alt="" width="100%" style="margin-top:95px">
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import {mapActions,mapState} from 'vuex'

export default {
  data() {
    return {
      lang: "",
      currency: "",
    };
  },
  computed: {
     ...mapState('main',['oktInfo']),
  },
  components: {
    HeadTop,
  },
  methods: {
    ...mapActions('main',['add_oktInfo']),
        changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.currency = locale;
      this.changeLanguage(locale);
      this.$router.push({
        name: this.$route.name,
        params: { lang: this.lang },
      });
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(this.$route.params.lang || "zhCHS");
    this.currency = this.$route.params.lang;
    this.add_oktInfo();
  },

};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#home {
  padding-bottom: 30px;
  padding-top: 70px;
}
.Popup {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.Popup .mark {
  width: 100%;
  height: 100%;
  background: #000000;
  opacity: 0.3;
}

.Popup .con {
  width: 94%;
  min-height: 120px;
  background: #fff;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%) scale3d(1, 1, 1);
  border-radius: 20px;
  overflow: hidden;
  box-sizing: border-box;
  padding: 0 20px;
}

.Popup .con .title {
  height: 60px;
  line-height: 60px;
  font-weight: bold;
}

.Popup .con .content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.Popup .con .content input {
  height: 50px;
  border: 0;
  background: #e9eefc;
  width: 100%;
  box-sizing: border-box;
  padding: 0 10px;
  border-radius: 10px;
  font-size: 16px;
  color: #000000;
}

.Popup .con .footer {
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Popup .con .footer .btn {
  height: 45px;
  width: 40%;
  color: #fff;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
}

.Popup .con .footer .cancel {
  background: #62698e;
}

.Popup .con .footer .confirm {
  background: #007bec;
}
/*弹层动画（放大）*/

.popIn {
  -webkit-animation: fadeleftIn 0.4s;
  animation: fadeleftIn 0.4s;
  -webkit-animation-name: popIn;
  animation-name: popIn;
}

@-webkit-keyframes popIn {
  0% {
    -webkit-transform: translate(-50%, -50%) scale3d(0, 0, 0);
    transform: translate(-50%, -50%) scale3d(0.5, 0.5, 0.5);
    opacity: 0;
  }
  50% {
    -webkit-animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
    animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) scale3d(1, 1, 1);
    transform: translate(-50%, -50%) scale3d(1, 1, 1);
    -webkit-animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    opacity: 1;
  }
}

@keyframes popIn {
  0% {
    -webkit-transform: translate(-50%, -50%) scale3d(0, 0, 0);
    transform: translate(-50%, -50%) scale3d(0.5, 0.5, 0.5);
    opacity: 0;
  }
  50% {
    -webkit-animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
    animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) scale3d(1, 1, 1);
    transform: translate(-50%, -50%) scale3d(1, 1, 1);
    -webkit-animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    opacity: 1;
  }
}
</style>



