        </div>

        <div class="bottom" >
          <div class="bottom-l">
            <div class="statistical-box">
              <span class="symbol">HBY{{ $t("message.LOCKEDusdt") }}</span>
            </div>
            <div class="statistical-box">
              <span class="num">{{HbyUsdtInfo ? HbyUsdtInfo.USDTtvl:0 | figureB }}</span>
            </div>
          </div>
          <div class="bottom-r">
            <div class="statistical-box">
              <span class="symbol">USDT{{ $t("message.LOCKEDusdt") }}</span>
            </div>
            <div class="statistical-box">
              <span class="num">{{ HbyUsdtInfo ? HbyUsdtInfo.HBYtvl:0  | figureW2 }}</span>
            </div>
          </div>
        </div>
        <div class="bottom" style=" margin-bottom: 12px">
          <div class="bottom-l">
            <div class="statistical-box">
              <span class="symbol">{{ $t("message.overallPledge") }}(LP)</span>
            </div>
            <div class="statistical-box">
              <span class="num">{{  poolInfo ? poolInfo.info[1] : 0 | figure }}</span>
