    </div>

        <!-- ht-HBY 挖che-->
    <div class="f-con">
      <!-- 头部 -->
      <div class="f-con-header">
        <div class="f-con-headerL">
          <img
            class="f-con-logo"
            src="../../assets/img/2.png"
            style="width: 40px; height: 40px; margin-right: 27px"
          />
          <img class="f-con-minlogo" src="../../assets/img/CUSE.png" />
          <div class="f-con-header-text">
            <p class="txt">HT+HBY LP</p>
            <span class=""> {{ $t("message.save") }} HT/HBY LP
            {{ $t("message.zyInput") }} CHE</span>
          </div>
         
        </div>
        <div class="zhedie" @click="hreftwo('ht')"><img src="../../assets/img/z.png" alt="" width="30"></div>
      </div>
      <!-- 四宫格 -->
      <div class="f-statistical" >
        <div class="bottom" >
          <!-- <div class="bottom-l">
            <div class="statistical-box">
              <span class="symbol">APY</span>
            </div>
            <div class="statistical-box">
              <span class="num" style="color:rgb(252 119 135);font-size: 20px;">{{  pool2Info? Apy :'0' | percent }}</span>
            </div>
          </div> -->
          <div class="bottom-l">
            <div class="statistical-box">
              <span class="symbol">{{ $t("message.theTotal") }}(CHE)</span>
            </div>
            <div class="statistical-box">
              <span class="num">{{ pool2Info ? pool2Info.curTotal : 0 }}</span>
            </div>
          </div>
          <div class="bottom-r">
            <div class="statistical-box">
              <span class="symbol">{{ $t("message.time") }}</span>
            </div>
            <div class="statistical-box">
              <span class="num">10 {{ $t("message.years") }}</span>
            </div>
          </div>
        </div>

        <div class="bottom" >
          <div class="bottom-l">
            <div class="statistical-box">
              <span class="symbol">HBY{{ $t("message.LOCKEDusdt") }}</span>
            </div>
            <div class="statistical-box">
              <span class="num">{{ HbyHtInfo ? HbyHtInfo.HBYtvl:0 | figureB }}</span>
            </div>
          </div>
          <div class="bottom-r">
            <div class="statistical-box">
              <span class="symbol">HT{{ $t("message.LOCKEDusdt") }}</span>
            </div>
            <div class="statistical-box">
              <span class="num">{{ HbyHtInfo ? HbyHtInfo.HTtvl:0  | figureW2 }}</span>
            </div>
          </div>
        </div>
        <div class="bottom" style=" margin-bottom: 12px">
          <div class="bottom-l">
            <div class="statistical-box">
              <span class="symbol">{{ $t("message.overallPledge") }}(LP)</span>
            </div>
            <div class="statistical-box">
              <span class="num">{{  pool2Info ? pool2Info.info[1] : 0 | figure }}</span>
            </div>
          </div>

          <div class="bottom-r">
            <div class="statistical-box">
              <span class="symbol">{{ $t("message.liu") }}(CHE)</span>
            </div>
            <div class="statistical-box">
              <span class="num">{{
                pool2Info ? pool2Info.info[15] : 0 | figure
              }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <FontCon />
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import FontCon from "../../components/Footer";
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: "",
      type: "home",
      timer2: null,
      // localR: "",
      // localR2: "",
      isApprove: false,
      wokt: "0",
      Apy: 0,
      isHome: true,
      stakeAmount: 0,
      hylink: "0x4b26a8d7be71b2a221e468e9f9f05d9af9c9229b",
      link: "--",
      isMaster: false,
      isShowEstablish: false,
      creationCode: "",
      cheApprove: false,
    };
  },
  computed: {
    ...mapState("main", [
      "pool2Info",
      "pool2Contract",
      "poolInfo",
      "poolContract",
      "account",
      "cheBalance",
      "HBYBalance",
      "cuseprice",
      "HBYCoinContract",
      "cheCoinContract",
      "HbyHtInfo",
      "HbyHtCoinContract",
      "HbyUsdtInfo",
      "HbyUsdtCoinContract",
    ]),
  },
  components: {
    HeadTop,
    FontCon,
  },
  methods: {
    ...mapActions("main", [
      "add_price",
      "add_pool2Info",
      "add_poolInfo",
      "add_HBYCoinContract",
      "add_cheCoinContract",
      "add_HbyHtCoinContract",
      "add_HbyUsdtCoinContract",
    ]),
    randomNum: function () {
      var code = "";
      for (var i = 1; i <= 6; i++) {
        const num = Math.floor(Math.random() * 10);
        code += num;
      }
      this.creationCode = code;
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + hash;
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    apyFun() {
      if(!this.pool2Info) return
      var all = (
        this.pool2Info.rewardRate *
        Number(this.cuseprice) *
        365
      ).toFixed(8);
      var zhiya = (this.pool2Info.info[1] / 10 ** 18).toFixed(2);
      
      if (all != 0 && zhiya != 0) {
        var apy = all / zhiya;
        this.Apy = apy;
      } else {
        return 0;
      }
    },
    cheApproveFun: function () {
      if (this.cheCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.pool2Info.address;
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var account = this.account;
      this.cheCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          // console.log("授权=====", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.cheApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权
    cheIsApproveFun: function () {
      if (this.pool2Info) {
        var account = this.account;
        var address = this.pool2Info.address;
        console.log(address)
        this.cheCoinContract.methods
          .allowance(account, address)
          .call()
          .then((res) => {
            var a = web3.utils.fromWei(res, "ether");
            if (a > 0) {
              this.cheApprove = true;
            }
          })
          .catch((err) => {
            this.$toast.clear();
            this.$toast(err.message);
            console.log(err);
          });
      }
    },
    max: function () {
      var num = this.HbyHtInfo.HbyHtBalance;
      num = num.toString();
      let index = num.indexOf(".");
      if (index !== -1) {
        num = num.substring(0, 4 + index + 1);
      } else {
        num = num.substring(0);
      }

      this.stakeAmount = num;
    },
    hidePop: function () {
      this.isShowEstablish = false;
      this.isShowJoin = false;
      this.creationCode = "";
      this.joinEstablish = "";
      this.partnerPop = false;
    },
    showPop: function (arg, callback) {
      // if (!this.cheBalance) return;
      if (arg == "showJoin") {
        if (this.$route.query.invite) {
          this.joinEstablish = this.$route.query.invite;
        }
        this.isShowJoin = true;
      } else if (arg == "showEstablish") {
        this.isShowEstablish = true;
      } else if (arg == "showPartnerPop") {
        this.partnerPop = true;
      } else if (arg == "show") {
        this.show = true;
      } else if (arg == "showPlay") {
        this.showPlay = true;
      }
    },
    // 		创建圈子
    establish: function (type) {
      if (this.pool2Info == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var myCode = 0; //我的圈子码/
      var code = 0; //创建圈子上级默认为0
      myCode = _this.creationCode;
      //2创建圈子
      if (!_this.creationCode) {
        this.$toast(this.$t("message.circleCode"));
        return;
      }
      if (Number(_this.cheBalance) < 10) {
        _this.$toast("CHE" + _this.$t("message.noBanner"));
        return;
      }

      _this.hidePop();
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      _this.pool2Contract.methods
        .isUsed(myCode)
        .call()
        .then((result) => {
          console.log(myCode,account,this.pool2Info.address)
          if (result) {
            _this.$toast(_this.$t("message.isUse"));
          } else {
            this.pool2Contract.methods
              .cretaePool(myCode)
              .send({ from: account })
              .then((res) => {
                _this.$toast.clear();
                _this.$toast.success(_this.$t("message.okCircle"));
                _this.add_pool2Info();
                // _this.getCoinBalance();
                _this.add_cheCoinContract();
                _this.getUrl(myCode);
                _this.isMaster = true;
                // console.log(myCode, _this.getUrl(myCode));
              })
              .catch((err) => {
                _this.$toast.clear();
                _this.$toast(err.message);
                console.log(err);
              });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getReward: function () {
      if (this.pool2Info == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var account = _this.account;
      this.pool2Contract.methods
        .getReward()
        .send({ from: account })
        .then((res) => {
          // console.log("getReward==============", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_pool2Info();
          // _this.getCoinBalance();
          _this.add_cheCoinContract();
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    //取消抵押
    withdraw: function () {
      if (this.pool2Info == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var myzy = web3.utils.fromWei(_this.pool2Info.info[6], "ether");
      if (myzy <= 0) {
        _this.$toast(_this.$t("message.noZy"));
        return;
      }
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.pool2Contract.methods
        .exit()
        .send({ from: account })
        .then((res) => {
          console.log("withdraw=======", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_pool2Info();
           _this.apyFun();
          _this.add_cheCoinContract();

          _this.withdrawAmount = 0;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    pirceInput(type) {
      if (type == 1) {
        let str = "" + this.stakeAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.stakeAmount = +str2;
        }
      } else {
        let str = "" + this.withdrawAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.withdrawAmount = +str2;
        }
      }
    },
    // 授权
    lpApprove: function () {
      if (this.pool2Info == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.pool2Info.address;
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var account = this.account;
      this.HbyHtCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("授权=====", res);
          if (res.status) {
            this.$toast.success(this.$t("message.accredit"));
            this.isApprove = true;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权
    isApproveFun: function () {
      if (this.pool2Info) {
        var account = this.account;
        var address = this.pool2Info.address;

        this.HbyHtCoinContract.methods
          .allowance(account, address)
          .call()
          .then((res) => {
            var a = web3.utils.fromWei(res, "ether");
            if (a > 0) {
              this.isApprove = true;
            }
            console.log("a==========", a);
          })
          .catch((err) => {
            this.$toast.clear();
            this.$toast(err.message);
            console.log(err);
          });
      }
    },
    stake: function () {
      if (this.pool2Info == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var code = _this.pool2Info.inviteCode || _this.pool2Info.referrer;
      if (!_this.isApprove) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }

      if (!code || code == "--") {
        code = _this.$route.query.invite
          ? _this.$route.query.invite.toString()
          : 1;
      }
      if (Number(_this.stakeAmount) > Number(_this.HbyHtInfo.HbyHtBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }
      if (Number(_this.balance / 10 ** 18) < 0) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (!code || code == "--") {
        _this.$toast(_this.$t("message.onId"));
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      this.$toast.loading({
        message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      // var monye = (_this.stakeAmount * 10 ** 18).toString();
      console.log(code,web3.utils.toWei(_this.stakeAmount, "ether"))
      this.pool2Contract.methods
        .investIn(code, web3.utils.toWei(_this.stakeAmount, "ether"))
        .send({ from: account })
        .then((res) => {
          if (res.status) {
            _this.$toast.clear();
            _this.$toast.success(_this.$t("message.dealSuccee"));
            _this.add_pool2Info();
            _this.apyFun();
            _this.add_cheCoinContract();
          }

          _this.stakeAmount = 1;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
      this.$router.push({
        name: this.$route.name,
        params: { lang: this.lang },
      });
    },
    hreftwo(url) {
      this.$router.push({ path: url  });
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(36, 42)
        : addr;
    },
  },
  mounted() {
    var _this = this;
    var lang = localStorage.getItem("lang");
    var newR = localStorage.getItem("r");
    this.changeLanguage(lang || "zhCHS");
    // if (newR) {
    //   this.localR = newR;
    // }

    // this.$dialog.alert({
    //   title: this.$t("message.TipTitle"),
    //   message: this.$t("message.TipCont"),
    //   confirmButtonText: this.$t("message.btnText"),
    //   theme: "round-button",
    // });
    if (!window.ethereum) {
      this.$notify({ type: "danger", message: this.$t("message.noMask") });
      return;
    } else {
      var web3 = new Web3(window.ethereum);
      web3.eth.net.getId().then((id) => {
        if (id != 128) {
          this.$notify({
            type: "danger",
            message: "Please switch the network!",
          });
          return;
        }
      });
    }
    this.add_price();
    var getData = () => {
      this.add_poolInfo();
      this.add_pool2Info();
      this.add_HBYCoinContract();
      this.add_cheCoinContract();
      this.add_HbyHtCoinContract();
      this.add_HbyUsdtCoinContract();
      this.apyFun();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (
          !this.HBYCoinContract ||
          !this.pool2Contract ||
          !this.poolContract ||
          !this.cheBalance ||
          !this.pool2Info 
        ) {
          getData();
        } else {
          this.isApproveFun();
          this.cheIsApproveFun();
           
          getData();
          // console.log(this.pool2Info);
          if (!_this.pool2Info.inviteCode && _this.$route.query.inviteCode) {
            _this.joinEstablish = _this.$route.query.inviteCode;
          }
          if (_this.pool2Info.inviteCode || _this.pool2Info.referrer) {
            var invite = _this.pool2Info.inviteCode || _this.pool2Info.referrer;
            _this.getUrl(invite);
          }

          var oldArr = this.pool2Info.poolAddr.toLocaleLowerCase();
          var hasIndexOf = oldArr.indexOf(this.account);
          if (hasIndexOf == 0) {
            this.isMaster = true;
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

